export default {
    formateDate(time){
        if(!time) return '';
        let date = new Date(time);
        let year = date.getFullYear(),
            month = date.getMonth() + 1,
            day = date.getDay() + 1,
            hours = date.getHours(),
            minutes = date.getMinutes(),
            seconds = date.getSeconds();
        return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
    }
}